import React,{Fragment} from "react"
import styled from "react-emotion"
import orange from '../images/orange2.png'
import github from '../images/github.png'
import twitter from '../images/twitter.png'
import qiita from '../images/qiita.png'
import face from '../images/face.png'
import SPHeader from './SPHeader'
import YouTube from 'react-youtube';

class Product extends React.Component {

  _onReady(event) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  }


  render() {

    const opts = {
      height: '290 ',
      width: '420',
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 1
      }
    };

    return (
      <Fragment>
        <SPHeader/>
        <Title></Title>
        <YoutubeWrapper>
        <YouTube
          videoId="coupzHx7gWk"
          opts={opts}
          onReady={this._onReady}
        />
        </YoutubeWrapper>
        <DescriptionWrapper>
          IC<br/>
          PythonFlaskAPIAPI<br/>
          Android<br/>
          <br/>
          <Slide href={"https://onedrive.live.com/view.aspx?resid=A95CFB7321695A07!25932&ithint=file%2cpptx&app=PowerPoint&authkey=!AK2X_3uWqAgJv7w"}></Slide>

        </DescriptionWrapper>
        <Title></Title>
        <YoutubeWrapper>
          <YouTube
            videoId="eVhEahEsQAs"
            opts={opts}
            onReady={this._onReady}
          />
        </YoutubeWrapper>
        <DescriptionWrapper>
          IoT<br/>
          IFTTT<br/>
          LINESlack<br/>
          Web<br/>
          react,go<br/>
          raspi9<br/>
          <Slide href={"https://docs.google.com/presentation/d/1ovTRKdkd11u6Z7DYVRaq1fOiCFx_erh79AN1KNfIjOg/edit?usp=sharing"}></Slide>

        </DescriptionWrapper>

        <Title></Title>
        <DescriptionWrapper>
          google home<br/>
          <br/>
          irMagician<br/>
          google home<br/>
          Qiita<br/>
          <Slide href={"https://qiita.com/mr_04v/items/dc6405d81e68e3550213"}></Slide>

        </DescriptionWrapper>

        <Title>ShareTask</Title>
        <DescriptionWrapper>
          Kotlin,React<br/>
          <br/>
          Coming soon...

        </DescriptionWrapper>

      </Fragment>
    )
  }
}



const YoutubeWrapper = styled("div")`
  position: sticky;
  text-align: center;
  
  @media (max-width: 420px) {
    margin-left: 75px;
  }
`;

const DescriptionWrapper = styled("div")`
  line-height: 180%;

  position: sticky;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  color: white;
  @media (max-width: 420px) {
    font-size: 18px;
    width:550px;
  }
  margin-bottom: 70px;
`;

const Slide = styled('a')`
  position: sticky;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  color: lightcoral;

  @media (max-width: 420px) {
    font-size: 20px;
    width:550px;
  }
`;

const Title = styled("div")`
      margin-top: 80px;

  position: sticky;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  color: orange;
  @media (max-width: 420px) {

    font-size: 20px;
    width:550px;
  }
`;


export default Product


